.map {
  height: 362px;
  position: relative;

  &::before {
    position: absolute;
    content: "";
    background: url("../img/map-marker.png") no-repeat center center/contain;
    width: 57px;
    height: 53px;
    z-index: 2;
    top: 0;
    left: 11px;
    bottom: 0;
    right: 0;
    margin: auto;
  }

  @media (min-width: $tabletWidth) {
    height: 400px;

    &::before {
      width: 113px;
      height: 106px;
    }
  }

  @media (min-width: $desktopWidth) {
    &::before {
      top: -34px;
      left: 490px;
    }
  }
}

.map__image {
  z-index: 1;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  object-fit: cover;
  margin: auto;
}

.map__iframe {
  z-index: 2;
  position: relative;
}
