.logo {
  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

.logo__image {
  width: 191px;
  height: 40px;

  @media (min-width: $tabletWidth) {
    width: 174px;
    height: 52px;
  }

  @media (min-width: $desktopWidth) {
    width: 202px;
    height: 59px;
  }
}

.logo__caption {
  width: 128px;
  height: 24px;

  @media (min-width: $tabletWidth) {
    width: 144px;
    height: 26px;
  }
}
